.loading {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  padding: 0 5px;
  background: #211f1f;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.loading_container {
  width: 100px;
  height: 32px;
  border-radius: 100px;
  background: #2c2c2c;
}

.loading_bar {
  height: 32px;
  animation: load 1s normal forwards;
  box-shadow: 0 10px 40px -10px #fffdfc;
  border-radius: 100px;
  background: #fffdfc;
  width: 0;
}

@keyframes load {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
